























import { Document } from '@/prismic/api';
import { Component, Vue, Prop } from 'vue-property-decorator';
import moment from 'moment';
import NavBar from '@/components/Navbar.vue'; // @ is an alias to /src
import HomeNav from '@/components/HomeNav.vue'; // @ is an alias to /src
import PrismicDOM from 'prismic-dom';
import Loading from '@/components/Loading.vue'

@Component({
    components: {
        Loading,
    }
})
export default class PostList extends Vue {
    @Prop() private startAt?: Number = 0;
    @Prop() private count?: Number = 20;
    @Prop() private orientation?: string;

    private get Posts(): Document[] {
        return this.$store.state.Posts.slice(this.startAt, this.count);
    }

    private get IsLoading(): Boolean {
        return this.$store.state.ArePostsLoading;
    }

    private get currentPost(): Document {
        return this.$store.state.CurrentPost;
    }

    private TimeForPost(time: string) : string {
        return moment.utc(time).local().format("MM-DD-YYYY");
    }

    private UrlForPost(post: Document): string {
        return `/blog/posts/${post.uid}`
    }
    private PostHtml(post: Document): string {
        return PrismicDOM.RichText.asHtml(post);
    }

    private GetPosts () {
        this.$store.dispatch("GetPosts");
    }

    private created () {
        this.GetPosts();
    }

    private isCurrent(post: Document): Boolean {
        return this.currentPost !== null && this.currentPost.id == post.id;
    }
}
